import React from 'react';

function About() {
  return (
    <div className="container py-5">
      <h2 className="mb-4 text-center">About Us</h2>

     
      <section className="mb-5">
        <h3>Our Clinic Background</h3>
        <p>
          Todays Dental Services has proudly served the community for over a decade,
          delivering compassionate and comprehensive dental care. Our state-of-the-art facility
          combines the latest technology with a warm, welcoming atmosphere to ensure your comfort and trust.
        </p>
      </section>

     
      <section className="mb-5 bg-light p-4 rounded">
        <h3>Our Mission</h3>
        <p>
          To provide exceptional dental care that enhances smiles and improves lives.
          We strive to create a friendly, comfortable environment where patients feel confident and cared for.
          Our goal is to educate and empower you for lifelong oral health.
        </p>
      </section>

      
      <section>
        <h3>Meet Our Practitioners</h3>
        <div className="row mt-4">
          <div className="col-md-4 text-center mb-4">
            <img
              src="data:image/jpeg;base64,"
              alt="Dr. Sarah Johnson"
              className="rounded-circle mb-3"
              style={{ width: '200px', height: '200px', objectFit: 'cover' }}
            />
            <h5>Dr. Sarah Johnson, DDS</h5>
            <p>Chief Dentist</p>
            <p>With over 15 years of experience, Dr. Johnson specializes in cosmetic and restorative dentistry, passionate about creating beautiful, healthy smiles.</p>
          </div>

          <div className="col-md-4 text-center mb-4">
            <img
              src="data:image/jpeg;base64,"
              alt="Dr. Michael Lee"
              className="rounded-circle mb-3"
              style={{ width: '200px', height: '200px', objectFit: 'cover' }}
            />
            <h5>Dr. Michael Lee, DDS</h5>
            <p>Orthodontist</p>
            <p>Expert in orthodontic treatments, Dr. Lee brings gentle care and modern techniques to align your teeth perfectly.</p>
          </div>

          <div className="col-md-4 text-center mb-4">
            <img
              src="data:image/jpeg;base64,"
              alt="Hygienist Emily Davis"
              className="rounded-circle mb-3"
              style={{ width: '200px', height: '200px', objectFit: 'cover' }}
            />
            <h5>Emily Davis, RDH</h5>
            <p>Registered Dental Hygienist</p>
            <p>Emily focuses on preventive care and patient education to maintain healthy gums and prevent dental issues.</p>
          </div>
        </div>
      </section>
    </div>
  );
}

export default About;
